import React, { useState } from 'react';
import PrimaryBlueButton from './primary-blue-button';
import OutlineWhiteButtonWide from './outline-white-button-wide';

const AdminPanel = () => {
    const [selectedForm, setSelectedForm] = useState(null);
    const [formData, setFormData] = useState({
        themeName: '',
        theoryText: '',
        questionText: '',
        correctAnswer: '',
        answerOptions: '',
        taskName: '',
        taskText: ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    //    
    const handleSave = () => {
            //    
            fetch('/api/admin/add', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({ formType: selectedForm, data: formData })
            })
                .then(response => response.json())
                .then(data => {
                    console.log('Success:', data);
                    alert('  ');
                    //     
                    setFormData({
                        themeName: '',
                        theoryText: '',
                        questionText: '',
                        correctAnswer: '',
                        answerOptions: '',
                        taskName: '',
                        taskText: ''
                    });
                })
                .catch((error) => {
                    console.error('Error:', error);
                    alert('  !');
                });
    };

    const renderForm = () => {
        switch (selectedForm) {
            case ' ':
                return (
                    <>
                        <label className="text20 Label"> </label>
                        <input
                            type="text"
                            name="themeName"
                            value={formData.themeName}
                            onChange={handleChange}
                            required
                            placeholder="   "
                            className="profile-textinput Small input"
                        />
                        <label className="text20 Label"> </label>
                        <textarea
                            rows="8"
                            name="theoryText"
                            value={formData.theoryText}
                            onChange={handleChange}
                            required
                            placeholder="    html-"
                            className="profile-textarea Small textarea"
                        ></textarea>
                        <div className="container10">
                            <div className="container11">
                                <PrimaryBlueButton button="" onClick={handleSave}></PrimaryBlueButton>
                            </div>
                        </div>
                    </>
                );
            case ' ':
                return (
                    <>
                        <label className="text20 Label"> </label>
                        <input
                            type="text"
                            name="themeName"
                            value={formData.themeName}
                            onChange={handleChange}
                            required
                            placeholder=" ,    "
                            className="profile-textinput Small input"
                        />
                        <label className="text20 Label"> </label>
                        <input
                            type="text"
                            name="questionText"
                            value={formData.questionText}
                            onChange={handleChange}
                            required
                            placeholder="  "
                            className="profile-textinput Small input"
                        />
                        <label className="text20 Label">   </label>
                        <input
                            type="text"
                            name="correctAnswer"
                            value={formData.correctAnswer}
                            onChange={handleChange}
                            required
                            placeholder="  "
                            className="profile-textinput Small input"
                        />
                        <label className="text20 Label">    </label>
                        <textarea
                            rows="8"
                            name="answerOptions"
                            value={formData.answerOptions}
                            onChange={handleChange}
                            required
                            placeholder="   "
                            className="profile-textarea Small textarea"
                        ></textarea>
                        <div className="container10">
                            <div className="container11">
                                <PrimaryBlueButton button="" onClick={handleSave}></PrimaryBlueButton>
                            </div>
                        </div>
                    </>
                );
            case ' ':
                return (
                    <>
                        <label className="text20 Label"> </label>
                        <input
                            type="text"
                            name="themeName"
                            value={formData.themeName}
                            onChange={handleChange}
                            required
                            placeholder=" ,    "
                            className="profile-textinput Small input"
                        />
                        <label className="text20 Label"> </label>
                        <input
                            type="text"
                            name="taskName"
                            value={formData.taskName}
                            onChange={handleChange}
                            required
                            placeholder="  "
                            className="profile-textinput Small input"
                        />
                        <label className="text20 Label"> </label>
                        <textarea
                            rows="8"
                            name="taskText"
                            value={formData.taskText}
                            onChange={handleChange}
                            required
                            placeholder="  "
                            className="profile-textarea Small textarea"
                        ></textarea>
                        <div className="container10">
                            <div className="container11">
                                <PrimaryBlueButton button="" onClick={handleSave}></PrimaryBlueButton>
                            </div>
                        </div>
                    </>
                );
            default:
                return (
                    <div className="HeadingOne head2"></div>
                );
        }
    };

    return (
        <div className="contact">
            <div className="container09">
            <div className="form">
                    <div className="HeadingOne head2">
                        {selectedForm ? `${selectedForm}` : '  '}
                    </div>
                    <span className="text19 Lead">
                            ,    
                    </span>
                    <form className="form1">
                        {renderForm()}
                    </form>

                </div>
                <div className="info">
                    <div className="container12">
                        <div className="HeadingTwo head3">  </div>
                        <span className="text24">
                            ,          ,          !
                        </span>
                        <div className="buttons-container">
                            <OutlineWhiteButtonWide text=" " onClick={() => setSelectedForm(' ')} />
                            <OutlineWhiteButtonWide text=" " onClick={() => setSelectedForm(' ')} />
                            <OutlineWhiteButtonWide text=" " onClick={() => setSelectedForm(' ')} />
                        </div>
                    </div>
                    <img alt="image" src="/wave-1.svg" className="profile-image1" />
                    <div className="container18"></div>
                </div>
            </div>
        </div>
    );
}

export default AdminPanel;