import { Helmet } from 'react-helmet'
import Cookies from 'js-cookie';
import React, {useEffect, useState} from 'react';

import HeaderFull from '../components/header-full'
import Footer from '../components/footer'
import ProgressPanel from '../components/progress-panel'
import ThemePanel from '../components/theme-panel'
import './tasks.css'
import Header from "../components/header";

const Tasks = (props) => {
    //    
    const [isAuthenticated, setIsAuthenticated] = useState(false);

    useEffect(() => {
        //    
        const user_id = Cookies.get('user_id');
        // console.log(user_id);
        if (user_id) {
            setIsAuthenticated(true);
            // console.log(parseInt(user_id, 10));
            // console.log(setIsAuthenticated);
        } else {
            setIsAuthenticated(false);
            // console.log(parseInt(user_id, 10));
        }
    }, []);

    //    
    const [selectedTheme, setSelectedTheme] = useState(null);
    const [isExpanded, setIsExpanded] = useState(false);
    const [questions, setQuestions] = useState([]);
    const [testStage, setTestStage] = useState('start'); //      
    const [selectedThemeId, setSelectedThemeId] = useState(null); // id   

    const [themes, setThemes] = useState([]); //    

    //   
    const handleThemeSelect = async (theme) => {
        setSelectedTheme(theme);
        //   theme_id,   
        Cookies.remove('theory_id');

        //    theory_id   theme_id
        Cookies.set('theory_id', theme.id);
        //setIsExpanded(true);

        setTestStage('start'); //   

        //        
        try {
            const response = await fetch('/api/questions');
            const data = await response.json();
            setQuestions(data);
        } catch (error) {
            console.error('   :', error);
        }
    };

    //     
    const handleTestResult = (result) => {
        if (result.correctAnswersCount === result.totalQuestions) {
            const updatedThemes = themes.map(theme =>
                theme.id === selectedThemeId ? {...theme, ischecked: true} : theme
            );
            setThemes(updatedThemes);
        }
    };

    //     
    const handleIconClick = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className="tasks-container">
            <Helmet>
                <title>PolySQL </title>
                <meta property="og:title" content="PolySQL " />
            </Helmet>

            {/*   */}
            {!isAuthenticated ? (
                <>
                    <div className="just_f"></div>
                    <Header />
                </>
            ) : (
                <HeaderFull />
            )}
            <div className="tasks-container1">
                <ProgressPanel
                    isHidden={isExpanded}
                    onThemeSelect={handleThemeSelect}
                    setSelectedThemeId={setSelectedThemeId}
                    selectedThemeId={selectedThemeId}

                    themes={themes}
                    setThemes={setThemes}
                />
                <ThemePanel
                    isExpanded={isExpanded}
                    onIconClick={handleIconClick}   //    
                    selectedTheme={selectedTheme}   //  
                    questions={questions}   //  
                    testStage={testStage} //  
                    setTestStage={setTestStage} //     
                    isAuthenticated = {isAuthenticated} //    

                    handleTestResult={handleTestResult}
                />
            </div>
            <Footer/>
        </div>
    );
}

export default Tasks
